/* index.css */
@tailwind base;
@tailwind components;
@tailwind utilities;


.App-footer {
  background-color: #282c34;
  color: white;
  padding: 1rem;
  text-align: center;
}

.App-footer ul {
  list-style: none;
  padding: 0;
}

.App-footer li {
  display: inline;
  margin: 0 1rem;
}

.App-footer a {
  color: white;
  text-decoration: none;
  font-size: 1.5rem; /* Adjust the size of the icons */
}

.App-footer a:hover {
  text-decoration: underline;
}

@keyframes rotate {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.rotate-logo {
  animation: rotate 5s linear infinite;
}

.profile-page {
  max-width: 800px;
  margin: auto;
  background-color: #f9fafb;
  border-radius: 10px;
  animation: float 4s ease-in-out infinite;
}

.player-info img {
  border-radius: 50%;
}

.badges-and-achievements ul,
.friends-ranking ul {
  margin-top: 10px;
}

.friends-ranking li {
  padding: 5px 0;
}

.settings {
  margin-top: 20px;
}

@keyframes float {
  0% { transform: translateY(0px); }
  50% { transform: translateY(-5px); }
  100% { transform: translateY(0px); }
}